.weather-app {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  margin: 0;
  height: 100vh;
  -webkit-animation: bg 4s infinite;
          animation: bg 4s infinite;
}

@-webkit-keyframes bg {
  0%, 15% {
    background: #f1f9fd;
  }
  25%, 40% {
    background: #c2c2c2;
  }
  50%, 65% {
    background: #ffefc5;
  }
  75%, 90% {
    background: #f7f7f7;
  }
}

@keyframes bg {
  0%, 15% {
    background: #f1f9fd;
  }
  25%, 40% {
    background: #c2c2c2;
  }
  50%, 65% {
    background: #ffefc5;
  }
  75%, 90% {
    background: #f7f7f7;
  }
}

.weather {
  overflow: hidden;
  position: relative;
  width: 14em;
  height: 14em;
  border-radius: 50%;
  -webkit-box-shadow: 2px 3px 15px grey;
          box-shadow: 2px 3px 15px grey;
  color: white;
}

.state {
  --x: 50%;
  --y: 50%;
  /*transform: translate(-50%, -50%);*/
  position: absolute;
  -webkit-transform: scale(1.02);
      -ms-transform: scale(1.02);
          transform: scale(1.02);
  fill: currentcolor;
  -webkit-animation: order 4s steps(1) infinite, clip 4s infinite;
          animation: order 4s steps(1) infinite, clip 4s infinite;
}
.state:nth-child(1) {
  --y: 155%;
  background: #5591b3;
  -webkit-animation-delay: -4s;
          animation-delay: -4s;
}
.state:nth-child(2) {
  --x: -55%;
  background: #4e4e4e;
  -webkit-animation-delay: -3s;
          animation-delay: -3s;
}
.state:nth-child(3) {
  --y: -55%;
  background: #ffd960;
  -webkit-animation-delay: -2s;
          animation-delay: -2s;
}
.state:nth-child(4) {
  --x: 155%;
  background: #bebebe;
  -webkit-animation-delay: -1s;
          animation-delay: -1s;
}

@-webkit-keyframes order {
  0%, 90% {
    z-index: 1;
  }
  15% {
    z-index: 0;
  }
}

@keyframes order {
  0%, 90% {
    z-index: 1;
  }
  15% {
    z-index: 0;
  }
}
@-webkit-keyframes clip {
  0%, 25%, 100% {
    -webkit-clip-path: circle(50%);
            clip-path: circle(50%);
  }
  25.001%, 90% {
    -webkit-clip-path: circle(50% at var(--x) var(--y));
            clip-path: circle(50% at var(--x) var(--y));
  }
}
@keyframes clip {
  0%, 25%, 100% {
    -webkit-clip-path: circle(50%);
            clip-path: circle(50%);
  }
  25.001%, 90% {
    -webkit-clip-path: circle(50% at var(--x) var(--y));
            clip-path: circle(50% at var(--x) var(--y));
  }
}
path {
  fill: none;
  stroke: currentcolor;
  stroke-width: 10px;
  stroke-linecap: round;
  stroke-linejoin: round;
}

.starshape {
  fill: url(#rg);
}



