.WorldMap {
  text-align: center;
}

.WorldMap-logo {
  -webkit-animation: WorldMap-logo-spin infinite 20s linear;
          animation: WorldMap-logo-spin infinite 20s linear;
  height: 0px;
}

.WorldMap-header {
  background-color: #222;
  height: 40px;
  padding: 10px;
  color: white;
}

.WorldMap-intro {
  font-size: large;
}

@-webkit-keyframes WorldMap-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes WorldMap-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

rect.overlay {
  opacity: 0;
}

rect.selection {
  fill: #FE9922;
  opacity: 0.5;
}

rect.handle {
  fill: #FE9922;
  opacity: 0.25;
}

path.countries {
  stroke-width: 1;
  stroke: #75739F;
  fill: #5EAFC6;
}



