.Overflow {
	text-align: left;
}

.div1, .div2, .div3, .div4 {
  border: 1px solid black;
  width:  250px;
  margin-bottom: 12px;
}

.div1 { overflow-x: hidden;}
.div2 { overflow-x: scroll;}
.div3 { overflow-x: visible;}
.div4 { overflow-x: auto;}


