.About {
  text-align: center;
}

.About-logo {
  -webkit-animation: About-logo-spin infinite 20s linear;
          animation: About-logo-spin infinite 20s linear;
  height: 80px;
}

.About-header {
  background-color: #222;
  height: 160px;
  padding: 20px;
  color: white;
}

.About-header > h3 {
  color: red;
}

.About-intro {
  font-size: large;
}

@-webkit-keyframes About-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes About-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}



